  constructor(public facturaService: FacturaService, public authService: AuthService, public usuarioService : UsuarioService, private toastr: ToastrService) {
    this.facturaService.getFacturas()
    .subscribe(res=>{
      this.facturaService.facturas = res as Contrato[];
      this.dataSource = new MatTableDataSource(this.facturaService.facturas);
      this.dataSource.sort = this.sort;
      this.dataSource.paginator = this.paginator;
    })
   }
 


 
  ngOnInit(): void {
    this.getFacturas();
    this.getUsuario();
  }
  getUsuario(){
    this.id = this.authService.getToken()
    this.authService.getUser(this.id)
    .subscribe(res => {
      this.usuario = res[0]

      console.log(this.usuario)
    })
  }
  getFacturas(){
    this.facturaService.getFacturas()
      .subscribe(res => {
      
        this.facturaService.facturas = res as Contrato[];
      },
      err => console.log(err)
      );
  }
  editFactura(contrato: Contrato ) {
    this.facturaService.selectedContrato = contrato;
    console.log( this.facturaService.selectedContrato.cedula)
  }
  addFactura(form: NgForm){
    if(form.valid){
      console.log(form.value)
      this.facturaService.putFactura(form.value)
      .subscribe(res =>{       
        this.toastr.success('Pago con xito!', 'Factura');
      })
     
    }
    else {
      this.toastr.error('Complete los campos', 'Por favor!');

    }
    this.getFacturas();
    
  }

